import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class AppController {
    constructor() {
        this.key = '';
        this.position = 0;
        this.size = 1024;
        this.mount = 12;
        this.model = undefined;
    }

    buildsearch() {
        AppView.render();
        this.model = new AppModel();
        function buildset() {
            const tag = document.querySelector('.search input').value;
            this.model.data = [];
            this.model.nextPageToken = '';
            this.buildsetData(tag, window.innerWidth);
        }
        document.querySelector('.search button').addEventListener('click', () => {
            buildset.call(this);
        });
        const input = document.querySelector('.search input');
        input.addEventListener('keydown', (event) => {
            if (event.keyCode === 13) {
                buildset.call(this);
                input.blur();
            }
        });
    }

    buildsetData(tag, size) {
        if (size < 540) {
            this.size = 300;
            this.mount = 3;
        } else if (size < 1024) {
            this.size = 540;
            this.mount = 6;
        } else {
            this.size = 1024;
            this.mount = 12;
        }
        this.model.update(tag).then(() => {
            this.position = 0;
            AppView.renderCards(this.model.data.slice(0, this.mount / 3 * 2));
            this.addEventScroll();
            document.querySelector('.nextpage').addEventListener('click', () => {
                this.next();
            });
            document.querySelector('.prevpage').addEventListener('click', () => {
                this.prev();
            });
            window.addEventListener('resize', () => {
                if (window.innerWidth < 540) {
                    if (this.size === 540) {
                        this.size = 300;
                        this.mount = 3;
                        this.resizeSet();
                    } else if (this.size === 1024) {
                        this.size = 300;
                        this.mount = 3;
                        this.resizeSet();
                    }
                } else if (window.innerWidth < 1024) {
                    if (this.size === 1024) {
                        this.size = 540;
                        this.mount = 6;
                        this.resizeSet();
                    } else if (this.size === 300) {
                        this.size = 540;
                        this.mount = 6;
                        this.resizeSet();
                    }
                } else if (this.size === 540) {
                    this.size = 1024;
                    this.mount = 12;
                    this.resizeSet();
                } else if (this.size === 300) {
                    this.size = 1024;
                    this.mount = 12;
                    this.resizeSet();
                }
            });
        })
            .catch(() => AppView.renderError());
    }

    resizeSet() {
        if (this.position % (this.mount / 3) !== 0) {
            this.position = Math.floor(this.position / (this.mount / 3)) * this.mount / 3;
        }
        if (this.position === 0) {
            AppView.redoRender(this.model.data.slice(this.position, this.mount),
                this.position / (this.mount / 3));
        } else {
            AppView.redoRender(this.model.data.slice(this.position - this.mount / 3,
                this.position - this.mount / 3 + this.mount),
            this.position / (this.mount / 3));
        }
        this.addEventScroll();
    }

    addEventScroll() {
        const cards = document.querySelectorAll('.set > div');
        const section = document.querySelector('div.wrapper');
        cards.forEach((elem) => {
            let isDown = false;
            let startX;
            elem.addEventListener('mousedown', (event) => {
                isDown = true;
                startX = event.pageX - elem.offsetLeft;
            });
            elem.addEventListener('mouseup', () => {
                isDown = false;
                const positionLeft = section.style.left;
                const currentPosition = positionLeft.split('%')[0];
                if (currentPosition < -140) this.next(positionLeft);
                else if (currentPosition > -60) this.prev(positionLeft);
                else this.current(positionLeft);
            });
            elem.addEventListener('mousemove', (event) => {
                window.getSelection().removeAllRanges();
                if (!isDown) return;
                const x = event.pageX - elem.offsetLeft;
                const walk = x - startX;
                if (this.position === 0 && walk > 0) {
                    section.style.left = '-100%';
                    return;
                }
                section.style.left = `${-100 + walk / window.innerWidth * 100}%`;
            });
            elem.addEventListener('dragstart', () => false);
            elem.addEventListener('touchstart', (event) => {
                isDown = true;
                startX = event.changedTouches[0].pageX - elem.offsetLeft;
            });
            elem.addEventListener('touchend', () => {
                isDown = false;
                const positionLeft = section.style.left;
                const currentPosition = positionLeft.split('%')[0];
                if (currentPosition < -140) this.next(positionLeft);
                else if (currentPosition > -60) this.prev(positionLeft);
                else this.current(positionLeft);
            });
            elem.addEventListener('touchmove', (event) => {
                window.getSelection().removeAllRanges();
                if (!isDown) return;
                const x = event.changedTouches[0].pageX - elem.offsetLeft;
                const walk = x - startX;
                if (this.position === 0 && walk > 0) {
                    section.style.left = '-100%';
                    return;
                }
                section.style.left = `${-100 + walk / window.innerWidth * 100}%`;
            });
        });
    }

    next(positionLeft) {
        AppView.animationChange('next', positionLeft);
        if (this.position + this.mount * 2 > this.model.data.length) this.model.update();
        setTimeout(() => {
            AppView.redoRender(this.model.data.slice(this.position,
                this.position + this.mount), this.position / (this.mount / 3) + 1);
            this.position += this.mount / 3;
            this.addEventScroll();
        }, 400);
    }

    prev(positionLeft) {
        AppView.animationChange('prev', positionLeft);
        setTimeout(() => {
            if (this.position - this.mount / 3 === 0) {
                AppView.redoRender(this.model.data.slice(0, this.mount), 0);
            } else {
                AppView.redoRender(this.model.data.slice(
                    this.position - (this.mount / 3 * 2),
                    this.position - (this.mount / 3 * 2) + this.mount,
                ),
                (this.position / (this.mount / 3) - 1));
            }
            this.position -= this.mount / 3;
            this.addEventScroll();
        }, 400);
    }

    current(positionLeft) {
        AppView.animationChange('cur', positionLeft);
        setTimeout(() => {
            this.addEventScroll();
        }, 500);
    }
}
