import './css/reset.css';
import './css/style.css';

export default class AppView {
    constructor(titles) {
        this.titles = titles;
        this.currentPage = 0;
    }

    static render() {
        document.body.innerHTML = `
        <main>
            <section class="options">
                <div class="search">
                    <input type="text"><button>Search</button>
                </div>
            </section>
        </main>`;
    }

    static renderCards(information) {
        const amountOneSlide = information.length / 2;
        let sectionCards = document.querySelector('section:nth-child(2)');
        let sectionNav = document.querySelector('section:nth-child(3)');
        if (sectionCards !== null) {
            sectionCards.className = 'cards';
            if (sectionNav !== null) sectionNav.style.display = 'flex';
            else {
                sectionNav = document.createElement('section');
                sectionNav.className = 'navigation';
                document.querySelector('main').appendChild(sectionNav);
            }
        } else {
            sectionCards = document.createElement('section');
            sectionCards.className = 'cards';
            document.querySelector('main').appendChild(sectionCards);
            sectionNav = document.createElement('section');
            sectionNav.className = 'navigation';
            document.querySelector('main').appendChild(sectionNav);
        }
        let html = '<div class="set prev"></div>';
        let dataSet = [];
        information.forEach((elem, index) => {
            dataSet.push(elem);
            if (index === amountOneSlide - 1) {
                html += this.renderSet(dataSet, 'cur');
                dataSet = [];
            } else if (index === amountOneSlide * 2 - 1) {
                html += this.renderSet(dataSet, 'next');
                dataSet = [];
            }
        });
        sectionCards.innerHTML = `<div class="wrapper" style="left: -100%">${html}</div>`;
        sectionNav.innerHTML = `
                <div class="prevpage" style="display: none;">0</div>
                <div class="curpage">1</div>
                <div class="nextpage">2</div>`;
    }

    static renderSet(dataSet, position) {
        let html = `<div class="set ${position}">`;
        dataSet.forEach((elem) => {
            html += `
                <div>
                    <a href="https://www.youtube.com/watch?v=${elem.id}" target="_blank">
                        <div class="preview" style="background-image: url('${elem.picture}')">
                            <h2>${elem.name}</h2>
                        </div>
                    </a>
                    <div class="about">
                        <div>${elem.channel}</div>
                        <div>${elem.date}</div>
                        <div>${elem.views}</div>
                    </div>
                    <div class="description">${elem.description}</div>
                </div>`;
        });
        return `${html}</div>`;
    }

    static redoRender(dataset, order) {
        let html = '';
        if (order === 0) {
            html = '<div class="set prev"></div>';
            html += this.renderSet(dataset.slice(0, dataset.length / 3), 'cur');
            html += this.renderSet(dataset.slice(dataset.length / 3, dataset.length / 3 * 2), 'next');
        } else {
            html += this.renderSet(dataset.slice(0, dataset.length / 3), 'prev');
            html += this.renderSet(dataset.slice(dataset.length / 3, dataset.length / 3 * 2), 'cur');
            html += this.renderSet(dataset.slice(dataset.length / 3 * 2, dataset.length), 'next');
        }
        const section = document.querySelector('section.cards');
        section.innerHTML = `
            <div class="wrapper" style="left: -100%">
                ${html}
            </div>`;
        const prevPage = document.querySelector('.prevpage');
        const curPage = document.querySelector('.curpage');
        const nextPage = document.querySelector('.nextpage');
        prevPage.textContent = order;
        curPage.textContent = order + 1;
        nextPage.textContent = order + 2;

        if (order === 0) {
            prevPage.style.display = 'none';
        } else {
            prevPage.style.display = 'block';
        }
    }

    static renderError() {
        let section = document.querySelector('section:nth-child(2)');
        const buttons = document.querySelector('section.navigation');
        if (section !== null) {
            section.innerHTML = '';
            if (buttons !== null) buttons.style.display = 'none';
        } else {
            section = document.createElement('section');
            document.querySelector('main').appendChild(section);
        }
        section.className = 'error';
        section.textContent = 'Something went wrong =(';
    }

    static animationChange(way, position) {
        const wrapper = document.querySelector('div.wrapper');
        const nextButton = document.querySelector('div.nextpage');
        const prevButton = document.querySelector('div.prevpage');
        nextButton.className = 'innective';
        prevButton.className = 'innective';
        if (way === 'next') {
            if (position === undefined) {
                wrapper.style.setProperty('--startleft', '-100%');
            } else {
                wrapper.style.setProperty('--startleft', position);
            }
            wrapper.style.setProperty('--endleft', '-200%');
        } else if (way === 'prev') {
            if (position === undefined) {
                wrapper.style.setProperty('--startleft', '-100%');
            } else {
                wrapper.style.setProperty('--startleft', position);
            }
            wrapper.style.setProperty('--endleft', '0%');
        } else {
            wrapper.style.setProperty('--startleft', position);
            wrapper.style.setProperty('--endleft', '-100%');
        }
        wrapper.className = 'wrapper change';
        setTimeout(() => {
            nextButton.className = 'nextpage';
            prevButton.className = 'prevpage';
            if (way === 'cur') {
                wrapper.className = 'wrapper';
                wrapper.style.left = '-100%';
            }
        }, 400);
    }
}
