export default class AppModel {
    constructor() {
        this.key = 'AIzaSyBsJbFDJwnmite3XpfGO3TJsT4kXmFYUAs';
        this.data = [];
        this.nextPageToken = '';
        this.tag = '';
    }

    static getDate(date) {
        const ms = Date.parse(date);
        return (new Date(ms)).toDateString();
    }

    static setViews(items, dataStat) {
        const itemsWithViews = items;
        dataStat.forEach((elem, index) => {
            itemsWithViews[index].views = elem.statistics.viewCount;
        });
        return itemsWithViews;
    }

    static sort(items) {
        return items.map(elem => ({
            name: elem.snippet.title,
            id: elem.id.videoId,
            channel: elem.snippet.channelTitle,
            date: AppModel.getDate(elem.snippet.publishedAt),
            description: elem.snippet.description,
            picture: elem.snippet.thumbnails.medium.url,
            views: '0',
        }));
    }

    async setData() {
        let token = '';
        if (this.nextPageToken.length !== 0) token = `&pageToken=${this.nextPageToken}`;
        const request = `https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&q=${this.tag.split(' ').join('%7C') + token}`;
        const { nextPageToken, items } = await (await fetch(request)).json();
        if (items.length === 0) throw new Error('bad request');
        this.nextPageToken = nextPageToken;
        const data = AppModel.sort(items);
        let ids;
        data.forEach((elem, index) => {
            if (index === 0) ids = elem.id;
            else ids += `,${elem.id}`;
        });
        const statRequest = `https://www.googleapis.com/youtube/v3/videos?key=${this.key}&id=${ids}&part=snippet,statistics`;
        const statitems = (await (await fetch(statRequest)).json()).items;
        return AppModel.setViews(data, statitems);
    }

    update(tag) {
        if (tag !== undefined) this.tag = tag;
        return this.setData()
            .then((newData) => {
                this.data = this.data.concat(newData);
            });
    }
}
